import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import Header from '../components/Header'
import { signup } from '../actions/userAction'
import { useDispatch, useSelector } from 'react-redux'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
const SignupScreen = (props) => {
 
  
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [role, setRole] = useState('')
  const dispatch = useDispatch()

  const userSignup = useSelector((store) => store.userSignup)
  const { loading, response, error } = userSignup

  // listen on change of the [loading, response, error] values
  useEffect(() => {
    console.log('use effect called: ')
    console.log('loading: ', loading)
    console.log('response: ', response)
    console.log('error: ', error)

    if (response && response.status === 'OK') {
      // user successfully got registered
      toast("SignUp successfully.... ", { type: "success" })
      props.history.push('/signin')
    } else if (error) {
      // there is an error while making the API call
      console.log(error)
      toast("SignUp Failed....Sorry", { type: "error" })
      //alert('error while making API call')
    }
  }, [loading, response, error])

  const onSignup = () => {
   
     console.log('role: ', role)
     console.log('email: ', email)
     console.log('password: ', password)
    dispatch(signup(email, password,role))
  }

  return (
    <div class="col-sm-8 " style={{paddingLeft:'300px'}}>
      <br></br>
      <Header title="Signup" />
      <div className="form">
        
        <div className="mb-3">
          <label className="form-label">Email</label>
          <input
            onChange={(e) => {
              setEmail(e.target.value)
            }}
            type="email"
            className="form-control"
            placeholder="test@test.com"
          />
        </div>
        <div className="mb-3">
          <label className="form-label">Password</label>
          <input
            onChange={(e) => {
              setPassword(e.target.value)
            }}
            type="password"
            className="form-control"
            placeholder="*****"></input>
        </div>
        <div className="mb-3">
      
        <br></br>

        <label><h6>Select Role : </h6></label>
                <div  className="form-group" onChange={(e) => {
                setRole(e.target.value)
              }}>
                    <input type="radio" value="client" name="role"/> Client
                    <br></br>
                    <input type="radio" value="company" name="role"/> Company
                   
                </div>
         
        </div>
        <div className="mb-3">
          <button onClick={onSignup} className="btn btn-success">
            Singup
          </button>
          <div className="float-end">
            <br></br>
            Existing user? <Link to="/signin">Signin here</Link>
          </div>
        </div>
      </div>

      {userSignup.loading && <div>waiting for result</div>}
    </div>
  )
}

export default SignupScreen
