import React, { Component } from 'react'
import ApiService from "../service/ApiService";
import CompanyDashboard from '../components/CompanyDashboard'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
class EditServiceComponent extends Component {

    constructor(props){
        super(props);
        this.state ={
            service_id:'',
            service_type:'',
            service_charges:'',
            status:'',
            companyId:''

            
        }
        this.saveService = this.saveService.bind(this);
        this.loadService = this.loadService.bind(this);
    }

    
    componentDidMount() {
        this.loadService();
    }

    loadService() {
        ApiService.fetchServiceById(parseInt(sessionStorage.getItem("editService")))
            .then((res) => {
                let service = res.data.result;
                this.setState({
                    service_id:  service.service_id,
                    service_type: service.service_type,
                    service_charges: service.service_charges,
                    status: service.status,
                    companyId: service.company.companyId
                
                })
            });
    }

    onChange = (e) =>
        this.setState({ [e.target.name]: e.target.value });


        
    saveService = (e) => {
        e.preventDefault();
        let s = {
            service_id:  this.state.service_id,
            service_type: this.state.service_type,
            service_charges: this.state.service_charges,
            status: this.state.status,
            "company" :{
                "companyId":parseInt(this.state.companyId)
           }
        
        
        };
        console.log(s)
        ApiService.editService(this.state.service_id,s)
            .then(res => {
                this.setState({message : 'Service updated successfully.'});
                toast("Service Edited Successfully..", { type: "info" });
                this.props.history.push('/list-service');
            }
            ,(err) => {
                console.log(err);
                toast("Failed to Edit !!!..try again", { type: "error" });
              });
    }

    render() {
        
        return( 
            <div>
               <CompanyDashboard/>
      <br></br>
      <h2 class="text-center">Edit Service</h2>
    <br></br>
      <div class="col-sm-8 " style={{paddingLeft:'300px'}} >
                <form>
                <div className="form-group">
                    <label><h6> Service Type : </h6> </label>
                    <input type="text" disabled placeholder="Service Type" name="service_type" className="form-control" value={this.state.service_type} onChange={this.onChange}/>
                </div>

                <div className="form-group">
                    <label><h6>Service Charges :</h6></label>
                    <input type="text" placeholder="Service Charges" name="service_charges" className="form-control" value={this.state.service_charges} onChange={this.onChange}/>
                </div>

                <div className="form-group">
                    <label><h6>Service Status:</h6></label>
                    <input type="text" disabled placeholder="Service Status" name="status" className="form-control" value={this.state.status} onChange={this.onChange}/>
                </div>

                <label><h6>Service Status : </h6></label>
                <div  className="form-group" onChange={this.setStatus}>
                    <input type="radio" value={this.state.status}    name="status"/> Available
                    <br></br>
                    <input type="radio" value={this.state.status}    name="status"/> NotAvailable
                    
                </div>
               
               
               

       
                <button className="btn btn-success" onClick={this.saveService}>Edit</button>
            </form>
            </div>
    </div>
        );
    }
}

export default EditServiceComponent;