import React, { Component } from 'react'
import ApiService from "../service/ApiService";
import ClientDashboard from '../components/ClientDashboard'
import Header from '../components/Header'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
class FeedbackComponent extends Component{
    constructor(props){
        super(props);
        this.state ={
            comments : '',
            experience: '',
            companyId:'',
            company:[],
            message:''
           
            
           
        }
        this.onCompanyChange = this.onCompanyChange.bind(this);
        this.setExperience =this.setExperience.bind(this);
        this.saveFeedback = this.saveFeedback.bind(this);
        this.reloadCompanyList = this.reloadCompanyList.bind(this);
        
    }
   
    componentDidMount() {
        this.reloadCompanyList();
    }

    reloadCompanyList() {
        ApiService.fetchCompany()
        .then((res) => {
            this.setState({company: res.data.result})
            console.log(this.state.company);
        });
        };
        onCompanyChange = (e) => {
          
            this.onChange(e);
          };

          setExperience = (e) => {
          
            this.onChange(e);
          };
    saveFeedback= (e) => {
        let uid=sessionStorage.getItem('userId')
        console.log(uid)
        ApiService.getClient(uid) 
        .then((res) => {
           
            console.log(res.data.result.clientId);
            sessionStorage.setItem('clientId',res.data.result.clientId)
        });
        let userId= sessionStorage.getItem("userId");
        let clientId= parseInt(sessionStorage.getItem("clientId"));
        let companyid=parseInt(this.state.companyId);
        console.log(this.state.companyId)
        console.log(companyid)
        console.log(this.state.comments)
        console.log(this.state.experience)
      
        e.preventDefault();
        let feedback = {comments : this.state.comments, experience: this.state.experience,
            "company": {
                        "companyId": companyid },
                      
            "client":{
                "clientId":clientId,
            }
        }
        console.log(feedback)
        ApiService.saveFeedback(feedback)
            .then(res => {
                this.setState({message : 'feedback added successfully.'});
                toast("Thanks For Feedback ....", { type: "success" })
                this.props.history.push('/client-dashboard');
           
           
            },
            (err) => {
                console.log(err);
                toast("Failed to add . Try again .....", { type: "error" });
              }
            );
    }

    onChange = (e) =>
        this.setState({ [e.target.name]: e.target.value });

    render() {
        return(
            <div>
               <ClientDashboard/>
      <br></br>
      <h2 class="text-center">Feedback </h2>
      <br></br>
      <div class="col-sm-8 " style={{paddingLeft:'300px'}}>
                <form>
                <div className="form-group">
                    <label> <h6>Comments :</h6></label>
                    <textarea type="text" rows="3" placeholder="comments" name="comments" className="form-control" value={this.state.comments} onChange={this.onChange}> </textarea>
                </div>


            <br></br>
            <label><h6>Experience : </h6></label>
                <div  className="form-group" onChange={this.setExperience}>
                    <input type="radio" value="average" name="experience"/> Average
                    <br></br>
                    <input type="radio" value="satifactory" name="experience"/> Satisfactory
                    <br></br>
                    <input type="radio" value="good" name="experience"/> Good
                    <br></br>
                    <input type="radio" value="Best" name="experience"/> Best
                </div>
               
               
                <div class="form-group">
                    <label for="exampleFormControlSelect1"><h6>Choose Company :</h6> </label>
                    <select
                    value={this.state.companyId}
                    onChange={this.onCompanyChange}
                        name="companyId"
                        class="form-control"
                        id="exampleSelect1"
                     >
                    <option value="" disabled>
                        Select company{" "}
                    </option>
                    {this.state.company.map((c) => (
                        <option value={c.companyId}>{c.company_name}</option>
                    ))}
            </select>
                </div>

                <button className="btn btn-success" onClick={this.saveFeedback}>Save</button>
            </form>
            </div>
    </div>
        );
    }
}

export default FeedbackComponent;