import {
    
    COMPANY_ADD_FAIL,
    COMPANY_ADD_SUCCESS,
    COMPANY_ADD_REQUEST,
  
 COMPANY_FETCH_REQUEST,
 COMPANY_FETCH_SUCCESS,
 COMPANY_FETCH_FAIL,
 COMPANY_FETCH_RESET
  } from "../constants/companyConstants.js";
  
  export const addProfileReducer = (state = {}, action) => {
    switch (action.type) {
      case COMPANY_ADD_REQUEST:
        return { loading: true };
      case COMPANY_ADD_SUCCESS:
        return { loading: false, response: action.payload };
      case COMPANY_ADD_FAIL:
        return { loading: false, error: action.payload };
      default:
        return state;
    }
  };
  


  export const fetchCompanyReducer = (state = {}, action) => {
    switch (action.type) {
      case COMPANY_FETCH_REQUEST:
        return { loading: true }
      case COMPANY_FETCH_SUCCESS:
        return { loading: false, response: action.payload }
      case COMPANY_FETCH_FAIL:
        return { loading: false, error: action.payload }
      case COMPANY_FETCH_RESET:
        return {}
      default:
        return state
    }
  }