//import './App.css'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import SigninScreen from './screens/SigninScreen'
import SignupScreen from './screens/SignupScreen'
import AdminDashboard from './screens/AdminDashboard'
import ClientDashboard from './screens/ClientDashboard'
import CompanyDashboard from './screens/CompanyDashboard'
import CompanyProfile from './screens/CompanyProfile'
import Navigation from './components/Navigation'
import QuotationDetailsComponent from './screens/QuotationDetails'
import ListServiceComponent from './screens/ListService'
import EditServiceComponent from './screens/EditService'
import AddService from './screens/AddService'
import AddClientComponent from './screens/ClientProfile'
import ListClientQuotationComponent from './screens/ClientQuotation'
import CompanyListComponent from './screens/CompanyList'
import CompanyDetailsComponent from './screens/CompanyDetails'
import AddQuotationComponent from './screens/AddQuotation'
import FeedbackComponent from './screens/Feedback'
import HomeScreen from './screens/HomeScreen'
import AboutScreen from './screens/AboutScreen'
import CompanyClientsComponent from './screens/CompanyClient'
import CompanyFeedbackComponent  from './screens/CompanyFeedback'
// import { ToastProvider, useToasts } from "react-toast-notifications";
import { ToastContainer, toast } from 'react-toastify'; //react public notification libraries
import 'react-toastify/dist/ReactToastify.css';
import 'react-app-polyfill/stable';
import store from './store';
import { Provider } from 'react-redux'
function App() {
  return (
    <Provider store={store}>
    <div>
  
      <Router>
        <Navigation />
        <ToastContainer />
      
        <div className="container">
          <Switch>
          {/* <Route path="/" component={SigninScreen} />  */}
          <Route path="/home" component={HomeScreen} /> 
            <Route path="/signin" component={SigninScreen} />
            <Route path="/signup" component={SignupScreen} />
             
            <Route path="/admin-dashboard" component={AdminDashboard} />
            <Route path="/client-profile" component={AddClientComponent} />
            <Route path="/client-dashboard" component={ClientDashboard} /> 
            <Route path="/client-quotations-list" component={ListClientQuotationComponent} /> 
            <Route path="/company-dashboard" component={CompanyDashboard} /> 
            <Route path="/list-company" component={CompanyListComponent} /> 
            <Route path="/company-profile" component={CompanyProfile} /> 
            <Route path="/quotation-details" component={QuotationDetailsComponent} /> 
             <Route path="/list-service" component={ListServiceComponent} />
             <Route path="/edit-service" component={EditServiceComponent} />
             <Route path="/add-service" component={AddService} />
             <Route path="/company-details" component={CompanyDetailsComponent} />
             <Route path="/add-quotation" component={AddQuotationComponent} />
             <Route path="/add-feedback" component={FeedbackComponent} />
             <Route path="/about" component={AboutScreen} />
             <Route path="/client-details" component={CompanyClientsComponent} />
             <Route path="/feedback" component={CompanyFeedbackComponent } />
          </Switch>
        </div>
      </Router>
    
    </div>
    </Provider>
  )
}

export default App

