import React, { Component } from 'react'
import ApiService from "../service/ApiService";
import ClientDashboard from '../components/ClientDashboard'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
class ListClientQuotationComponent extends Component {

    constructor(props) {
        super(props)
        this.state = {
            quotation: [],
            message: null,
            cid:''
        }
         this.deleteQuotation = this.deleteQuotation.bind(this);
       this.addQuotation=this.addQuotation.bind(this)
        this.reloadQuotationList = this.reloadQuotationList.bind(this);
    }

    componentDidMount() {
        this.reloadQuotationList();
    }

    reloadQuotationList() {
        let uid=sessionStorage.getItem('userId')
        console.log(uid)
        ApiService.getClient(uid) 
        .then((res) => {
            //this.setState({quotation: res.data.result})
            console.log(res.data.result.clientId);
            this.setState({cid: res.data.result.clientId})
            console.log(this.state.cid)
            sessionStorage.setItem("client",res.data.result.clientId)
           
        });
        
       let cid=parseInt(sessionStorage.getItem("client"))
       console.log(cid)
        ApiService.fetchQuotation(cid)
            .then((res) => {
                this.setState({quotation: res.data.result})
                console.log(this.state.quotation);
                toast("Client Quotation List..", { type: "warning" });
            },
            (err) => {
             console.log(err);
             toast("Failed to Fetch . Try again .....", { type: "error" });
           });
        
    }

    deleteQuotation(qId) {
        ApiService.deleteQuotation(qId)
           .then(res => {
               this.setState({message : 'quotation deleted successfully.'});
               this.setState({quotation: this.state.quotation.filter(q => q.quotationId !== qId)});
               toast("Quotation  Deleted Successfully..", { type: "success" });
           },
           (err) => {
            console.log(err);
            toast("Failed to delete . Try again .....", { type: "error" });
          });

    }

   

    addQuotation() {
      
        this.props.history.push('/list-company');
    }

    render() {
        return (
            <div>
                  <ClientDashboard/>
                <h2 className="text-center">Quotation Details</h2>
                <button className="btn btn-primary" style={{width:'200px',height:'50px'}} onClick={() => this.addQuotation()}> Add Quotation</button>
               <br></br>
                <table className="table table-striped">
                    <thead>
                        <tr>
                        <th>Id</th>
                        <th>Title</th>
                        <th>Address</th>
                        <th>Shift To</th>
                        <th>Shift From</th>
                        {/* <th>Size</th> */}
                        <th>Shift Date</th>
                        <th>Shift Time</th>
                        {/* <th>Service </th> */}
                        <th>Status</th>
                        <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.quotation.map(
                        q =>
                                    <tr >
                                    <td>{q.quotationId}</td>
                                        <td>{q.quotationName}</td>
                                        <td>{q.address}</td>
                                        <td>{q.shift_to}</td>
                                        <td>{q.shift_from}</td>
                                        <td>{q.shift_date}</td>
                                        <td>{q.shift_time}</td>
                                        {/* <td>{user.service}</td> */}
                                        <td>{q.status}</td>
                                        <td>
                                             <button className="btn btn-dark" disabled={q.status==="delivered"} onClick={() => this.deleteQuotation(q.quotationId)}>Cancel</button>
                                           
                                        </td>
                                    </tr>
                            )
                        }
                    </tbody>
                </table>

            </div>
        );
    }

}

export default ListClientQuotationComponent;