import {
    USER_SIGNIN_FAIL,
    USER_SIGNIN_REQUEST,
    USER_SIGNIN_SUCCESS,
    USER_SIGNOUT,
    USER_SIGNUP_FAIL,
    USER_SIGNUP_REQUEST,
    USER_SIGNUP_SUCCESS,
  
 
  
  } from "../constants/userConstants";
  import axios from "axios";
  import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
  //import { toast } from "react-toastify";
  
  /***********************logout***************************** */
  
  export const logout = () => {
    return (dispatch) => {
      sessionStorage.removeItem("userId");
      sessionStorage.removeItem("company_id");
      sessionStorage.removeItem("serviceId");
      sessionStorage.removeItem("client");
      sessionStorage.removeItem("company");
      sessionStorage.removeItem("companyquotation")
      dispatch({ type: USER_SIGNOUT });
      toast("Logout Succesfully ..", { type: "info" });
      document.location.href = "/signin";
    };
  };
  /***********************signUp***************************** */
  
  export const signup = (
   
    email,
    password,
    role
   
  ) => {
    return (dispatch) => {
      
      dispatch({
        type: USER_SIGNUP_REQUEST,
      });
      const header = {
        headers: {
          "Content-Type": "application/json",
        },
      };
      const body = {
        email,
        password,
        role
      };
      const url = "http://3.17.161.158:7070/user/register";
      axios
        .post(url, body, header)
        .then((response) => {
          dispatch({
            type: USER_SIGNUP_SUCCESS,
            payload: response.data,
          });
        })
        .catch((error) => {
          dispatch({
            type: USER_SIGNUP_FAIL,
            payload: error,
          });
        });
    };
  };
  /***********************SignIn ***************************** */
  
  export const signin = (email, password) => {
    return (dispatch) => {
      sessionStorage.removeItem("userId");
      sessionStorage.removeItem("company_id");
      sessionStorage.removeItem("serviceId");
      sessionStorage.removeItem("client");
      sessionStorage.removeItem("company");
      sessionStorage.removeItem("companyquotation")
      dispatch({
        type: USER_SIGNIN_REQUEST,
      });
      const header = {
        headers: {
          "Content-Type": "application/json",
        },
      };
      const body = {
        email,
        password
      };
      const url = "http://3.17.161.158:7070/user/login";
      axios
        .post(url, body, header)
        .then((response) => {
          dispatch({
            type: USER_SIGNIN_SUCCESS,
            payload: response.data,
          });
        })
        .catch((error) => {
          dispatch({
            type: USER_SIGNIN_FAIL,
            payload: error,
          });
        });
    };
  };
 