import React, { Component } from 'react'
import ApiService from "../service/ApiService";
import CompanyDashboard from '../components/CompanyDashboard'
import Header from '../components/Header'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
class QuotationDetailsComponent extends Component {

    constructor(props) {
        super(props)
        this.state = {
            quotations: [],
            message: null,
            quotationName:'',
            address:'',
            shift_to:'',
            shift_from:'',
            size:'',
            shift_date:'',
            shift_time:'',
            cid:'',
            receiver:''






          
        }
          this.approvedQuotation = this.approvedQuotation.bind(this);
         this.rejectQuotation= this.rejectQuotation.bind(this);
         this.deliverQuotation= this.deliverQuotation.bind(this);
        this.reloadQuotationList = this.reloadQuotationList.bind(this);
    }

    componentDidMount() {
        this.reloadQuotationList();
    }

    reloadQuotationList() {
        let uid=sessionStorage.getItem('userId')
        console.log(uid)
        ApiService.getCompany(uid) 
        .then((res) => {
            //this.setState({quotation: res.data.result})
            console.log(res.data.result.companyId);
           // this.state.cid=res.data.result.companyId
           // this.setState({cid:res.data.result.companyId})
          //  console.log(this.state.cid)
           sessionStorage.setItem('companyquotation',res.data.result.companyId)
        });
        let cid=parseInt(sessionStorage.getItem('companyquotation'))

        ApiService.fetchCompanyQuotation(cid)
            .then((res) => {
            
                toast("Quotation Details List..", { type: "warning" });
             
              console.log(res)
                this.setState({quotations: res.data.result})
                console.log(this.state.quotations);
            });
        
    }

    rejectQuotation(quotation) {
     
      let q={
        quotationId :quotation.quotationId,
        quotationName :quotation.quotationName ,
        address:quotation.address,
        shift_from: quotation.shift_from,
        shift_to:quotation.shift_to,
        shift_time:quotation.shift_time,
        shift_date:quotation.shift_date,
        size:quotation.size, 
        receiver:quotation.clients.user.email,
        status :"rejected" ,
        
        "clients": {
          "clientId": quotation.clients.clientId
         } ,
       "services":{
            "service_id":quotation.services.service_id
          } ,
      "company" :{
              "companyId":parseInt(sessionStorage.getItem('companyId'))
         }
      }
      console.log(q)
      ApiService.editQuotation(quotation.quotationId,q)
      .then(res => {
       
          toast(" Quotation Rejected", { type: "error" });
          this.reloadQuotationList()
          this.setState({message : 'status edited successfully.'});
        
         
      })
       

    }

    deliverQuotation(quotation) {


     let q={
        quotationId :quotation.quotationId,
        quotationName :quotation.quotationName ,
        address:quotation.address,
        shift_from: quotation.shift_from,
        shift_to:quotation.shift_to,
        shift_time:quotation.shift_time,
        shift_date:quotation.shift_date,
        size:quotation.size, 
        receiver:quotation.clients.user.email,
        status :"delivered" ,
        
        "clients": {
          "clientId": quotation.clients.clientId
         } ,
       "services":{
            "service_id":quotation.services.service_id
          } ,
      "company" :{
              "companyId":parseInt(sessionStorage.getItem('companyId'))
         }
      }
      console.log(q)
      ApiService.editQuotation(quotation.quotationId,q)
      .then(res => {
        toast(" Quotation Delivered", { type: "info" });
        this.reloadQuotationList()
          this.setState({message : 'status edited successfully.'});
         
         
         
      })

    }
    approvedQuotation(quotation)
    {

     
      let q={
        quotationId :quotation.quotationId,
        quotationName :quotation.quotationName ,
        address:quotation.address,
        shift_from: quotation.shift_from,
        shift_to:quotation.shift_to,
        shift_time:quotation.shift_time,
        shift_date:quotation.shift_date,
        size:quotation.size, 
        receiver:quotation.clients.user.email,
        status :"approved" ,
        
        "clients": {
          "clientId": quotation.clients.clientId
         } ,
       "services":{
            "service_id":quotation.services.service_id
          } ,
      "company" :{
              "companyId":parseInt(sessionStorage.getItem('companyId'))
         }
      }
      console.log(q)
      ApiService.editQuotation(quotation.quotationId,q)
      .then(res => {
        console.log(res)
        toast(" Quotation Approved", { type: "success" });
        this.reloadQuotationList()
          this.setState({message : 'status edited successfully.'});
         
         
         
      })
    }
render(){
  return (
    <div>
        <CompanyDashboard/>
        <br></br>
      <Header title="Quotation Details" />
     

      <table className="table table-striped" style={{width:'1500px', marginLeft:'-180px'}}>
        <thead>
          <tr>
            <th>Id</th>
            <th>Title</th>
            <th>Address</th>
            <th>Shift To</th>
            <th>Shift From</th>
            <th>Size</th>
            <th>Shift Date</th>
            <th>Shift Time</th>
            <th>Client Email</th>
            <th>Contact No.</th>
            <th>Service </th>
            <th>Status</th>
            <th>Action</th>

          </tr>
        </thead>
        <tbody>
        {
           this.state.quotations.map(
            quotation =>
                <tr>
                  <td>{quotation.quotationId}</td>
                  <td>{quotation.quotationName}</td>
                  <td>{quotation.address}</td>
                  <td>{quotation.shift_to}</td>
                  <td>{quotation.shift_from}</td>
                   <td>{quotation.size}</td>
                   <td>{quotation.shift_date}</td>
                   <td> {quotation.shift_time}</td>
                   <td>{quotation.clients.user.email}</td>
                   <td>{quotation.clients.mobile_no}</td>
                   <td>{quotation.services.service_type}</td>
                  
                   <td>{quotation.status}</td>
                   
                   <td>
                   <button className="btn btn-info" disabled={quotation.status==="approved" || quotation.status==="delivered" } onClick={() => this.approvedQuotation(quotation)}>Accept</button>
                   <button className="btn btn-danger" disabled={quotation.status==="delivered" || quotation.status==="rejected" } onClick={() =>  this.rejectQuotation(quotation)} style={{marginLeft: '4px'}}>Reject</button>
                   <button className="btn btn-success"  disabled={quotation.status==="pending" || quotation.status==="rejected" } onClick={() => this.deliverQuotation(quotation)} style={{marginLeft: '4px'}}>Deliver</button>
                   </td>
                </tr>
              )
            }
        </tbody>
      </table>
    </div>
  )
}
}

export default QuotationDetailsComponent
