import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import Header from '../components/Header'
import { signin } from '../actions/userAction'
import { useDispatch, useSelector } from 'react-redux'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries


const SigninScreen = (props) => {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')

  const userSignin = useSelector((store) => store.userSignin)
  const { loading, error, response } = userSignin

  const dispatch = useDispatch()
  const onSignin = () => {
  
    dispatch(signin(email, password))
  }

  useEffect(() => {
    if (response && response.status === 'OK') {
      
       sessionStorage.setItem('userId', response.result.userId)
      if(response.result.role === "admin")
      {
          toast("SignIn successfully....Welcome ", { type: "success" })
            props.history.push('/admin-dashboard')
      }
      else if(response.result.role === "client")
      {
          toast("SignIn successfully....Welcome ", { type: "success" })
            props.history.push('/client-dashboard')
      }
      else 
      {
             toast("SignIn successfully....Welcome ", { type: "success" })
            props.history.push('/company-dashboard')
      }
    } else if (response && response.status === 'error') {
      toast("Invalid Email or Password!!!..try again", { type: "error" });
    // alert(response.error)
    } else if (error) {
      toast("Login Failed!!!..try again", { type: "error" });
      // alert("...."+error)
    }
  }, [loading, error, response])

  return (
    
    <div class="col-sm-8 " style={{paddingLeft:'300px'}}>
      <br></br>
      <Header title="Signin" />

      <div className="form">
        <div className="mb-3">
          <label className="form-label">Email</label>
          <input
            onChange={(e) => {
              setEmail(e.target.value)
            }}
            type="email"
            className="form-control"
            placeholder="test@test.com"
            required
          />
        </div>
        <div className="mb-3">
          <label className="form-label">Password</label>
          <input
            onChange={(e) => {
              setPassword(e.target.value)
            }}
            type="password"
            className="form-control"
            placeholder="*****"
            required
            ></input>
        </div>
        <div className="mb-3">
          <button onClick={onSignin} className="btn btn-success">
            Singin
          </button>
          <div className="float-end">
            New User? <Link to="/signup">Signup here</Link>
          </div>
        </div>
      </div>
      {loading && <div>waiting for response</div>}
    </div>
  )
}

export default SigninScreen
