import React, { Component } from 'react'
import ApiService from "../service/ApiService";
import CompanyDashboard from '../components/CompanyDashboard'
import Header from '../components/Header'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
class ListServiceComponent extends Component {

    constructor(props) {
        super(props)
        this.state = {
            services: [],
            message: null,
            cid:''
        }
          this.deleteService = this.deleteService.bind(this);
         this.editService= this.editService.bind(this);
       
        this.reloadServiceList = this.reloadServiceList.bind(this);
    }

    componentDidMount() {
        this.reloadServiceList();
    }

    reloadServiceList() {
        let uid=sessionStorage.getItem('userId')
        console.log(uid)
        ApiService.getCompany(uid) 
        .then((res) => {
            //this.setState({quotation: res.data.result})
            console.log(res)
            console.log(res.data.result.companyId);
           // this.state.cid=res.data.result.companyId
           // console.log(this.state.cid);
            sessionStorage.setItem('cid',res.data.result.companyId)
        });
        let cid=parseInt(sessionStorage.getItem('cid'))
        console.log(cid);
        ApiService.getService(cid)
            .then((res) => {
              console.log(res)
          // this.state.services=res.data.result.company.services
                this.setState({services: res.data.result.company.services})
                console.log(this.state.services);
                toast("Service Details List..", { type: "warning" });
            },(err) => {
              console.log(err);
              toast("Failed to load!!!..try again", { type: "error" });
            }
            
            );
        
    }

    deleteService(sId) {
        ApiService.deleteService(sId)
           .then(res => {
               this.setState({message : 'service deleted successfully.'});
               this.setState({services: this.state.services.filter(s => s.service_id !== sId)});
               toast("Service Delete Succesfully..", { type: "success" });
           }
           ,(err) => {
            console.log(err);
            toast(" Failed to delete !!!..try again", { type: "error" });
          });

    }

    editService(sId)
    {
      sessionStorage.setItem("editService",sId);
      this.props.history.push('/edit-service');
    }

    render() {
  return (
    <div>
      <CompanyDashboard/>
      <br></br>
      <Header title="Service Details" />
     

      <table className="table table-striped">
        <thead>
          <tr>
            <th>Id</th>
            <th>Type</th>
            <th>Charges</th>
            <th>Status</th>
            <th>Action</th>

          </tr>
        </thead>
        <tbody>
        {
           this.state.services.map(
            service =>
               <tr >
                  <td>{service.service_id}</td>
                  <td>{service.service_type}</td>
                  <td>{service.service_charges}</td>
                  <td>{service.status}</td>
                  
                   
                   <td>
                   <button className="btn btn-danger" onClick={() => this.deleteService(service.service_id)}>Delete</button>
                   <button className="btn btn-warning"  style={{marginLeft: '20px'}} onClick={() => this.editService(service.service_id)}>Edit</button>
                 
                 
                   </td>
                </tr>
              )
            }
        </tbody>
      </table>
    </div>
  )

     }
}
export default ListServiceComponent
