import {
    
    SERVICE_ADD_REQUEST ,
    SERVICE_ADD_SUCCESS ,
    SERVICE_ADD_FAIL ,
    SERVICE_ADD_RESET, 

    SERVICE_FETCH_REQUEST,
    SERVICE_FETCH_SUCCESS ,
    SERVICE_FETCH_FAIL,
    SERVICE_FETCH_RESET

   
 
 } from "../constants/serviceConstants.js";

export const addServiceReducer = (state = {}, action) => {
   switch (action.type) {
     case SERVICE_ADD_REQUEST:
       return { loading: true }
     case SERVICE_ADD_SUCCESS:
       return { loading: false, response: action.payload }
     case SERVICE_ADD_FAIL:
       return { loading: false, error: action.payload }
     case SERVICE_ADD_RESET:
       return {}
     default:
       return state
   }
 }
 
 export const fetchServiceReducer = (state = {}, action) => {
   switch (action.type) {
     case SERVICE_FETCH_REQUEST:
       return { loading: true }
     case SERVICE_FETCH_SUCCESS:
       return { loading: false, response: action.payload }
     case SERVICE_FETCH_FAIL:
       return { loading: false, error: action.payload }
     case SERVICE_FETCH_RESET:
       return {}
     default:
       return state
   }
 }
 