import axios from 'axios';



class ApiService {

    addCompany(c)
    {
        return axios.post('/company/add', c);
    }

    addClient(client) {
        return axios.post('/client/add', client);
    }
    
    fetchQuotation(clientid){
        console.log(clientid)
        return axios.get('/quotation/get_quotation_by_clientId/'+clientid);
    }
    
    getClient(uid) 
    {
        return axios.get('/client/getclientbyuserid/'+uid);
    }

    getCompany(uid) 
    {
        console.log(uid)
        return axios.get('/company/getcompanybyuserid/'+uid);
    }

    fetchServiceById(sId)
    {
        return axios.get('/service/'+sId);
    }


    fetchCompanyFeedback(cid)
    {
        return axios.get('/feedback/get_feedback_by_companyId/'+cid);
    }
    editService(sid,s)
    {
    
        console.log(sid)
        console.log(s)
       return axios.put('/service/'+s.service_id , s)
    }

    fetchCompany()
    {

        return axios.get('/company');
    }

    // fetchService(cid)
    // {
    //     return axios.get('http://localhost:7070/service/'+cid);
    // }
    viewCompany(cId)
    {
        return axios.get('/company/'+cId);
    }

    addQuotation(quotation)
    {
        return axios.post('/quotation/add',quotation);
    }

    getService(companyId)
    {
        console.log(companyId)
        return axios.get('/service/getservicebycompanyid/'+companyId);
    }
    
    saveFeedback(feedback)
    {
        return axios.post('/feedback/add',feedback);
    }

    addService(s)
    {
        console.log(s)
        return axios.post('/service/add',s);
    }
    deleteQuotation(qId)
    {
        return axios.delete('/quotation/'+qId);
    }

    deleteService(sId)
    {
        return axios.delete('/service/'+sId);
    }


   

    fetchCompanyQuotation(cid)
    {
        console.log(cid)
        return axios.get('/quotation/get_quotation_by_companyId/'+cid);
    }


    editQuotation(qId,q)
    {
        return axios.put('/quotation/'+qId , q);
    }
   
}

export default new ApiService();