import {
    
     QUOTATION_ADD_REQUEST ,
 QUOTATION_ADD_SUCCESS ,
 QUOTATION_ADD_FAIL ,
 QUOTATION_ADD_RESET, 

 QUOTATION_FETCH_REQUEST,
 QUOTATION_FETCH_SUCCESS ,
 QUOTATION_FETCH_FAIL,
 QUOTATION_FETCH_RESET
  
    
  
  } from "../constants/QuotationConstants.js";

export const addQuotationReducer = (state = {}, action) => {
    switch (action.type) {
      case QUOTATION_ADD_REQUEST:
        return { loading: true }
      case QUOTATION_ADD_SUCCESS:
        return { loading: false, response: action.payload }
      case QUOTATION_ADD_FAIL:
        return { loading: false, error: action.payload }
      case QUOTATION_ADD_RESET:
        return {}
      default:
        return state
    }
  }
  
  export const fetchQuotationReducer = (state = {}, action) => {
    switch (action.type) {
      case QUOTATION_FETCH_REQUEST:
        return { loading: true }
      case QUOTATION_FETCH_SUCCESS:
        return { loading: false, response: action.payload }
      case QUOTATION_FETCH_FAIL:
        return { loading: false, error: action.payload }
      case QUOTATION_FETCH_RESET:
        return {}
      default:
        return state
    }
  }
  