import React, { Component } from 'react'
import ApiService from "../service/ApiService";
import ClientDashboard from '../components/ClientDashboard'
import { Link } from 'react-router-dom'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
class CompanyListComponent extends Component {

    constructor(props) {
        super(props)
        this.state = {
            company: [],
            c:null,
            message: null
        }
        // this.deleteUser = this.deleteUser.bind(this);
         this.viewCompany = this.viewCompany.bind(this);
        // this.addUser = this.addUser.bind(this);
        this.reloadQuotationList = this.reloadCompanyList.bind(this);
    }

    // viewCompany(id) {
    //     window.localStorage.setItem("userId", id);
    //     this.props.history.push('/company-info');
    // }


    componentDidMount() {
        this.reloadCompanyList();
    }

    reloadCompanyList() {
        let uid=sessionStorage.getItem('userId')
        console.log(uid)
        ApiService.getClient(uid) 
        .then((res) => {
            //this.setState({quotation: res.data.result})
            console.log(res.data.result.clientId);
            sessionStorage.setItem('clientId',res.data.result.clientId)
        });
        let cid=parseInt(sessionStorage.getItem('clientId'))

        ApiService.fetchCompany()
            .then((res) => {
                this.setState({company: res.data.result})
                toast("Company List..", { type: "warning" });
                console.log(this.state.company);
            },
            (err) => {
             console.log(err);
             toast("Failed to Fetch . Try again .....", { type: "error" });
           });
        
    }

    viewCompany(cId) {
        ApiService.viewCompany(cId)
           .then(res => {
           
            if(res)
            {
                toast("Company Details..", { type: "warning" });
                this.setState({c : res.data.result});
                sessionStorage.setItem('company_id',res.data.result.companyId);
                sessionStorage.setItem('company_name',res.data.result.company_name);
                sessionStorage.setItem('company_email',res.data.result.company_email);
                sessionStorage.setItem('company_address',res.data.result.address);
                sessionStorage.setItem('company_city',res.data.result.city);
                sessionStorage.setItem('company_state',res.data.result.state);
                sessionStorage.setItem('company_pin_code',res.data.result.pin_code);
                sessionStorage.setItem('company_contact_no',res.data.result.contact_no)
                // console.log(res.data.result)
                this.props.history.push('/company-Details')
              
            } 
           },
           (err) => {
            console.log(err);
            toast("Failed to Fetch . Try again .....", { type: "error" });
          });

    }

   
   
    render() {
        return (
            <div>
                  <ClientDashboard/>
                  <br></br>
                <h2 className="text-center">Company List</h2>
                <br></br>
                <div class="row">
                {
                     this.state.company.map(
                        q =>
                        <div class="col-sm-4"  >
                        <div class="card" class="card text-white bg-info mb-3 card text-center">
                          
                          <div class="card-body" text-align="center">
                            <h4 class="card-title">Name :{q.company_name}</h4>
                            <p class="card-text">Email : {q.company_email}</p>
                            {/* <p class="card-text">Created Time :{{b.created_time}}</p> */}
                           <p> <button className="btn btn-warning" onClick={() => this.viewCompany(q.companyId)}> Visit</button></p>
                          </div>
                        </div>
                        </div>
                        
                        )
                }
         </div>
            </div>
        );
    }

}

export default CompanyListComponent;