import React, { Component } from 'react'
import ApiService from "../service/ApiService";
import ClientDashboard from '../components/ClientDashboard'
import Header from '../components/Header'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
class AddQuotationComponent extends Component{
    constructor(props){
        super(props);
        this.state ={
            quotationName: '',
            address: '',
            shift_from: '',
            shift_to:'',
            status:'',
            size:'',
            shift_time:'',
            shift_date:'',
            clientid:''
            
           
        }
        this.state = {isClientExist: false};
        this.saveQuotation = this.saveQuotation.bind(this);
        this.reloadClient=this.reloadClient.bind(this);
      }
     
      componentDidMount() {
          this.reloadClient();
      }
    
      reloadClient() {
          let uid=sessionStorage.getItem('userId')
          console.log(uid)
          ApiService.getClient(uid) 
          .then((res) => {
              //this.setState({quotation: res.data.result})
              
              console.log(res.data.result.clientId);
             
              if(res.data.result.clientId != null)
              {
                this.setState({isClientExist: true});
             
              
  
              }
             

              
              
          });
             
             
          
      }

   
    saveQuotation = (e) => {
        let uid=sessionStorage.getItem('userId')
        console.log(uid)
        ApiService.getClient(uid) 
        .then((res) => {
           
            console.log(res.data.result.clientId);
           // this.state.clientid=res.data.result.clientId
            this.setState({clientid:res.data.result.clientId})
            
        });
       
        let companyId= sessionStorage.getItem("company_id");
        let serviceId= sessionStorage.getItem("serviceId");

        e.preventDefault();
        let q = {
                  quotationName: this.state.quotationName, 
                  address: this.state.address,
                  shift_from: this.state.shift_from,
                  shift_to: this.state.shift_to,
                  shift_time:this.state.shift_time,
                  shift_date:this.state.shift_date,
                  size:this.state.size,

            "clients": {
                        "clientId":parseInt(this.state.clientid)
                       } ,
            "services":{
                          "service_id":serviceId
                        } ,
            "company" :{
                            "companyId":companyId
                       }


            };
        ApiService.addQuotation(q)
            .then(res => {
                this.setState({message : 'quotation added successfully.'});
                toast("Quotation added successfully ..", { type: "success" });
                this.props.history.push('/client-quotations-list');
            },
            (err) => {
             console.log(err);
             toast("Failed to Add Quotation . Try again .....", { type: "error" });
           });
    }

    onChange = (e) =>
        this.setState({ [e.target.name]: e.target.value });

    render() {
        return( 
            <div>
               <ClientDashboard/>
      <br></br>
     
      <h2 class="text-center">Add Quotation </h2>
      { !this.state.isClientExist && (

          <h2 class="text-center">Please Create your profile first </h2>
       )}
    <br></br>
    { this.state.isClientExist && ( 
      <div class="col-sm-8 " style={{paddingLeft:'300px'}} >
                <form>
                <div className="form-group">
                    <label><h6> Quotation Name</h6> </label>
                    <input type="text" placeholder="Quotation Name" name="quotationName" className="form-control" value={this.state.quotationName} onChange={this.onChange}/>
                </div>

                <div className="form-group">
                    <label><h6>Address:</h6></label>
                    <input type="text" placeholder="address" name="address" className="form-control" value={this.state.address} onChange={this.onChange}/>
                </div>

                <div className="form-group">
                    <label><h6>Shift From :</h6></label>
                    <input type="text" placeholder="Shift From " name="shift_from" className="form-control" value={this.state.shift_from} onChange={this.onChange}/>
                </div>

                <div className="form-group">
                    <label><h6>Shift To :</h6></label>
                    <input type="text" placeholder="Shift To" name="shift_to" className="form-control" value={this.state.shift_to} onChange={this.onChange}/>
                </div>

                <div className="form-group">
                    <label> <h6>Size :</h6></label>
                    <input type="text" placeholder="Size" name="size" className="form-control" value={this.state.size} onChange={this.onChange}/>
                </div>


                <div class="form-group row">
            <label for="example-date-input" class="col-2 col-form-label">
              <h6>Date: </h6>
            </label>
            <div class="col-10">
              <input
                class="form-control"
                type="date"
                name="shift_date"
                value={this.state.shift_date}
                onChange={this.onChange}
              ></input>
            </div>
          </div>

          <div class="form-group row">
            <label for="example-time-input" class="col-2 col-form-label">
              <h6>Time: </h6>
            </label>
            <div class="col-10">
              <input
                class="form-control"
                type="time"
                name="shift_time"
                value={this.state.shift_time}
                onChange={this.onChange}
              ></input>
            </div>
          </div>
          <div>

          </div>
                <button className="btn btn-success" onClick={this.saveQuotation}>Add</button>
            </form>
            </div>
    )}
    </div>
      
        );
    }
}

export default AddQuotationComponent;