import React, { Component } from 'react'
import ApiService from "../service/ApiService";
import CompanyDashboard from '../components/CompanyDashboard'
import Header from '../components/Header'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
class CompanyClientsComponent extends Component {

    constructor(props) {
        super(props)
        this.state = {
            quotations: [],
            message: null
        }
         
        this.reloadClientList = this.reloadClientList.bind(this);
    }

    componentDidMount() {
        this.reloadClientList();
    }

    reloadClientList() {
        let uid=sessionStorage.getItem('userId')
        console.log(uid)
        ApiService.getCompany(uid) 
        .then((res) => {
            //this.setState({quotation: res.data.result})
            console.log(res.data.result.companyId);
            sessionStorage.setItem('company',res.data.result.companyId)
        });
        let cid=parseInt(sessionStorage.getItem('company'))

        ApiService.fetchCompanyQuotation(cid)
            .then((res) => {
              toast("Company Clients..", { type: "success" });
                this.setState({quotations: res.data.result})
                console.log(this.state.quotations);
            },
            (err) => {
             console.log(err);
             toast("Failed to Fetch . Try again .....", { type: "error" });
           });
        
    }

   

   
   
render(){
  return (
    <div>
        <CompanyDashboard/>
        <br></br>
      <Header title="Client Details" />
     

      <table className="table table-striped" >
        <thead>
          <tr>
           
            <th>Client Email</th>
            <th>Mobile No</th>
            <th>Address</th>
            

          </tr>
        </thead>
        <tbody>
        {
           this.state.quotations.map(
            quotation =>
                <tr>
                
                   <td>{quotation.clients.user.email}</td>
                   <td>{quotation.clients.mobile_no}</td>
                   <td>{quotation.clients.address}</td>
                  
                  
                   
                  
                </tr>
              )
            }
        </tbody>
      </table>
    </div>
  )
}
}

export default CompanyClientsComponent
