import React, { Component } from 'react'
import ApiService from "../service/ApiService";
import CompanyDashboard from '../components/CompanyDashboard'
import Header from '../components/Header'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
class CompanyProfileComponent extends Component{
    constructor(props){
        super(props);
        this.state ={
          company_name:'',
          company_email:'',
          address:'',
          city:'',
          state:'',
          pin_code:'',
          contact_no:'',
          userid:'',
          cid:''
            
           
        }
        this.state = {isCompanyExist: false};
        this.onAddCompany = this.onAddCompany.bind(this);
        this.reloadCompany= this.reloadCompany.bind(this);
    }
   

    componentDidMount() {
      this.reloadCompany();
  }

  reloadCompany() {
      let uid=sessionStorage.getItem('userId')
      console.log(uid)
      ApiService.getCompany(uid) 
      .then((res) => {
          //this.setState({quotation: res.data.result})
          console.log(res.data.result.companyId);
          if(res.data.result.companyId != null)
          {
                this.state.company_name=res.data.result.company_name
                this.state.company_email=res.data.result.company_email
                this.state.address=res.data.result.address
                this.state.city=res.data.result.city
                this.state.state=res.data.result.state
                this.state.pin_code=res.data.result.pin_code
                this.state.contact_no=res.data.result.contact_no
          }
          if(res.data.result.companyId != null)
          {
            toast("Company Profile..", { type: "info" });
            this.setState({isCompanyExist: true});
          }
         
      });
   
    

    

      
  }

  onChange = (e) =>
        this.setState({ [e.target.name]: e.target.value });
    onAddCompany = (e) => {
        let uid=sessionStorage.getItem('userId')
        console.log(uid)
       
       
      

        e.preventDefault();
        let c = {
          company_name:this.state.company_name, 
          company_email: this.state.company_email,
          address: this.state.address,
          city: this.state.city,
          state:this.state.state,
          pin_code:this.state.pin_code,
          contact_no:this.state.contact_no,

            "user": {
                        "userId": uid 
                       } 


            };
        ApiService.addCompany(c)
            .then(res => {
                this.setState({message : 'company  added successfully.'});
                toast("Profile created successfully..", { type: "success" });
                this.props.history.push('/company-dashboard');
            }
            ,(err) => {
              console.log(err);
              toast("Failed to Created Profile!!!..try again", { type: "error" });
            });
    }

    onEditCompany = (e) => {
    }
        render(){
  return (
    <div>
      <CompanyDashboard/>
      <br></br>
    
      <h2 class="text-center">Company Profile </h2>
      { this.state.isCompanyExist && (  
               
               
               <div class="container p-3 my-3 bg-dark text-white col-sm-6" >
               <h2>Name      :  { this.state.company_name}</h2>
               <h4>Email     :  {this.state.company_email}</h4>
               <h4>City      :  {this.state.city}</h4>
               <h4>Address   :  {this.state.address}</h4>
               <h4>State     :  {this.state.state}</h4>
               <h4>Pin-Code  :  {this.state.pin_code}</h4>
               <h4>ContactNo.:  {this.state.contact_no}</h4>
               <div className="mb-3">
                <button onClick={this.onEditCompany} className="btn btn-primary" style={{width:'100px', marginLeft:'250px'}}>
               Edit
                </button>
        
              </div>
               </div>
      )}
      <br></br>
      { !this.state.isCompanyExist && (  
        <div class="col-sm-8 " style={{paddingLeft:'300px'}}>
      <div className="form">
        
        <div className="mb-3">
          <label className="form-label">Company Name</label>
          <input
           onChange={this.onChange}
            type="text"
            name="company_name"
            className="form-control"
            placeholder="Company Name"
          />
        </div>
        <div className="mb-3">
          <label className="form-label">Company Email</label>
          <input
           onChange={this.onChange}
            type="email"
            name="company_email"
            className="form-control"
            placeholder="test@test.com"></input>
        </div>
        <div className="mb-3">
        <label className="form-label">Address</label>
          <input
           onChange={this.onChange}
            type="text"
            name="address"
            className="form-control"
            placeholder="address"></input>
        </div>
        <div className="mb-3">
        <label className="form-label">City</label>
          <input
           onChange={this.onChange}
            type="text"
            name="city"
            className="form-control"
            placeholder="city"></input>
        </div>

        <div className="mb-3">
        <label className="form-label">State</label>
          <input
            onChange={this.onChange}
            type="text"
            name="state"
            className="form-control"
            placeholder="state"></input>
        </div>
        <div className="mb-3">
        <label className="form-label">Pin Code </label>
          <input
           onChange={this.onChange}
            type="text"
            name="pin_code"
            className="form-control"
            placeholder="Pin -Code"></input>
        </div>
        <div className="mb-3">
        <label className="form-label">Contact No.</label>
          <input
           onChange={this.onChange}
            type="text"
            name="contact_no"
            className="form-control"
            placeholder="Contact No."></input>
        </div>
        <div className="mb-3">
          <button onClick={this.onAddCompany} className="btn btn-success">
            Submit
          </button>
        
        </div>
      </div>
      </div>
 )} 
     
      
    </div>
  )
}
}
export default CompanyProfileComponent

