import React, { Component } from 'react'
import ApiService from "../service/ApiService";
import ClientDashboard from '../components/ClientDashboard'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
class CompanyDetailsComponent extends Component {

    constructor(props) {
        super(props)
        this.state = {
            company:null,
            service:[],
            message: null,
            cid:"",
            cname:"",
            cemail:"",
            caddress:"",
            ccity:"",
            cstate:"",
            cpin:"",
            ccontact:""
        }
       
         this.addQuotation = this.addQuotation.bind(this);
         this.reloadServiceList = this.reloadServiceList.bind(this);
    }

    componentDidMount() {
        this.reloadServiceList();
    }

    reloadServiceList() {
         this.state.cid=parseInt(sessionStorage.getItem('company_id'));
         this.state.cname=sessionStorage.getItem('company_name');
         this.state.cemail=sessionStorage.getItem('company_email');
         this.state.caddress=sessionStorage.getItem('company_address');
         this.state.ccity=sessionStorage.getItem('company_city');
         this.state.cstate=sessionStorage.getItem('company_state');
         this.state. cpin=sessionStorage.getItem('company_pin_code');
         this.state. ccontact=sessionStorage.getItem('company_contact_no');
        console.log( this.state.cid)
        ApiService.getService(this.state.cid) 
        .then((res) => {
            
            toast("Company Service List ...", { type: "success" });
            this.setState({service: res.data.result.company.services})
            // console.log(this.state.service)
            // console.log(res.data.result.company);
            // console.log(res.data.result.company.services);
           
        },
        (err) => {
         console.log(err);
         toast("Failed to Fetch . Try again .....", { type: "error" });
       });
        
     }

   

   

    addQuotation(sId) {
        sessionStorage.setItem("serviceId",sId)
        this.props.history.push('/add-quotation');
    }

    render() {
        return (
            <div>
                  <ClientDashboard/>
                  <br></br>
                <h2 className="text-center">Company Details</h2>
               
               
              <div class="container p-3 my-3 bg-dark text-white col-sm-6" >
              <h2>Name      :  {this.state.cname}</h2>
              <h4>Email     :  {this.state.cemail}</h4>
              <h4>City      :  {this.state.ccity}</h4>
              <h4>Address   :  {this.state.caddress}</h4>
              <h4>State     :  {this.state.cstate}</h4>
              <h4>Pin-Code  :  {this.state.cpin}</h4>
              <h4>ContactNo.:  {this.state.ccontact}</h4>
              </div>
              <div class="row">
                {
                     this.state.service.map(
                        s =>
                        <div class="col-sm-4"  >
                        <div class="card" class="card bg-info card text-blue  mb-3 card text-center">
                          
                          <div class="card-body" text-align="center">
                            <h4 class="card-title">Type :   {s.service_type}</h4>
                            <p class="card-text">Charges:   {s.service_charges}</p>
                            <p class="card-text">status :   {s.status}</p>
                            {/* <p class="card-text">Created Time :{{b.created_time}}</p> */}
                           <p> <button className="btn btn-warning" disabled={s.status==="NotAvailable"} onClick={() => this.addQuotation(s.service_id)}  > Add Quotation</button></p>
                          </div>
                        </div>
                        </div>
                        
                        )
                }
         </div>
            </div>
        );
    }

}

export default CompanyDetailsComponent;