import React, { Component } from 'react'
import ApiService from "../service/ApiService";
import CompanyDashboard from '../components/CompanyDashboard'
import Header from '../components/Header'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries

class CompanyFeedbackComponent extends Component {

    constructor(props) {
        super(props)
        this.state = {
            feedback: [],
            message: null
        }
         
        this.reloadFeedbackList = this.reloadFeedbackList.bind(this);
    }

    componentDidMount() {
        this.reloadFeedbackList();
    }

    reloadFeedbackList() {
        let uid=sessionStorage.getItem('userId')
        console.log(uid)
        ApiService.getCompany(uid) 
        .then((res) => {
            //this.setState({quotation: res.data.result})
            console.log(res.data.result.companyId);
            sessionStorage.setItem('companyId',res.data.result.companyId)
        });
        let cid=parseInt(sessionStorage.getItem('companyId'))

        ApiService.fetchCompanyFeedback(cid)
            .then((res) => {
              toast("Client Feedback ..", { type: "info" });
                this.setState({feedback: res.data.result})
                console.log(this.state.feedback);
            },
            (err) => {
             console.log(err);
             toast("Failed to Fetch . Try again .....", { type: "error" });
           });
        
    }

   

   
   
render(){
  return (
    <div>
        <CompanyDashboard/>
        <br></br>
      <Header title="Clients Feeback" />
     

      <table className="table table-striped" >
        <thead>
          <tr>
           
            <th>Experience</th>
            <th>Comments</th>
            <th>Client Email</th>
            <th>Mobile No</th>
            

          </tr>
        </thead>
        <tbody>
        {
           this.state.feedback.map(
            f =>
                <tr>
                
                   <td>{f.experience}</td>
                   <td>{f.comments}</td>
                   <td>{f.client.user.email}</td>
                   <td>{f.client.mobile_no}</td>
                  
                   
                  
                </tr>
              )
            }
        </tbody>
      </table>
    </div>
  )
}
}

export default CompanyFeedbackComponent 
