
import React, { Component } from 'react'
import ApiService from "../service/ApiService";
import CompanyDashboard from '../components/CompanyDashboard'
import Header from '../components/Header'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
class AddService extends Component{
    constructor(props){
        super(props);
        this.state ={
          service_type: '',
          service_charges: '',
          status: '',
          companyid:''
            
           
        }
        this.saveService = this.saveService.bind(this);
    }
   

   
    saveService = (e) => {
      let uid=sessionStorage.getItem('userId')
      console.log(uid)
      ApiService.getCompany(uid) 
      .then((res) => {
        
          console.log(res.data.result.companyId);
         // this.state.companyid=res.data.result.companyId;
        //  this.setState({companyid :res.data.result.companyId});
         sessionStorage.setItem("cs",res.data.result.companyId)
      });
       
      console.log(sessionStorage.getItem("cs"))
        e.preventDefault();
        let s = {
                  
          service_type: this.state.service_type,
          service_charges: this.state.service_charges,
          status: this.state.status,
            "company" :{
                           // "companyId":parseInt( this.state.companyid)
                           "companyId":parseInt(sessionStorage.getItem("cs"))
                       }


            };
        ApiService.addService(s)
            .then(res => {
              console.log(res)
                this.setState({message : 'service added successfully.'});
                toast("Service Added Successfully..", { type: "success" });
                this.props.history.push('/list-service');
            },
            (err) => {
             console.log(err);
             toast("Failed to Add Service . Try again .....", { type: "error" });
           });
    }

    setStatus = (e) => {
          
      this.onChange(e);
    };
    onChange = (e) =>
        this.setState({ [e.target.name]: e.target.value });

  render() {
  return (
    <div>
      <CompanyDashboard/>
      <br></br>
      <Header title="Add Service" />
      <div className="form">
        
        <div className="mb-3">
          <label className="form-label">Service Type</label>
          <input
           onChange={this.onChange}
            type="text"
            name="service_type"
            className="form-control"
            value={this.state.service_type}
            placeholder="Service Type"
          />
        </div>
        <div className="mb-3">
          <label className="form-label">Service Charges</label>
          <input
           onChange={this.onChange}
            type="text"
            name="service_charges"
            className="form-control"
            value={this.state.service_charges}
            placeholder="Service Charges"></input>
        </div>
        
        <label><h6>Status : </h6></label>
                <div  className="form-group" onChange={this.setStatus}>
                    <input type="radio" value="available" name="status"/> Available
                    <br></br>
                    <input type="radio" value="NotAvailable" name="status"/> NotAvailable
                   
                </div>
               

       <div className="mb-3">
          <button onClick={this.saveService} className="btn btn-success">
            Submit
          </button>
        
        </div>
      </div>

     
    </div>
  )}
}

export default AddService 

