import React, { Component } from 'react'
import ApiService from "../service/ApiService";
import ClientDashboard from '../components/ClientDashboard'
import { ToastContainer, toast } from "react-toastify"; //react public notification libraries
class AddClientComponent extends Component{
    constructor(props){
        super(props);
        this.state ={
            mobile_no: '',
            address: '',
            email:''
            
           
        }
        this.state = {isClientExist: false};
        this.saveClient = this.saveClient.bind(this);
        this.reloadClient=this.reloadClient.bind(this);
    }
   
    componentDidMount() {
        this.reloadClient();
    }
  
    reloadClient() {
        let uid=sessionStorage.getItem('userId')
        console.log(uid)
        ApiService.getClient(uid) 
        .then((res) => {
            //this.setState({quotation: res.data.result})
            
            console.log(res.data.result.clientId);
            if(res.data.result.clientId)
            {
            this.state.mobile_no=res.data.result.mobile_no;
            this.state.address=res.data.result.address;
            this.state.email=res.data.result.user.email;
            }
            console.log(this.state.mobile_no)
            console.log(this.state.address)
            console.log(this.state.email)
            if(res.data.result.clientId != null)
            {
              this.setState({isClientExist: true});
              toast("Client Profile..", { type: "info" });
           
            

            }
            
            
        },
        (err) => {
         console.log(err);
         toast("Failed to Fetch . Try again .....", { type: "error" });
       });
           
           
        
    }
   
    saveClient = (e) => {
        let userId= sessionStorage.getItem("userId");

        e.preventDefault();
        let client = {mobile_no: this.state.mobile_no, address: this.state.address,"user": {
          "userId": userId }};
        ApiService.addClient(client)
            .then(res => {
                this.setState({message : 'Client added successfully.'});
                toast("Profile Created Successfully..", { type: "success" });
                this.props.history.push('/client-dashboard');
            },
            (err) => {
             console.log(err);
             toast("Failed to Create Profile. Try again .....", { type: "error" });
           });
    }

    onEditClient=(e)=>
    {}
    onChange = (e) =>
        this.setState({ [e.target.name]: e.target.value });

    render() {
        return(
            <div>
               <ClientDashboard/>
      <br></br>
      <h2 class="text-center">Client Profile </h2>

      { this.state.isClientExist && (  
               
               
               <div class="container p-3 my-3 bg-dark text-white col-sm-6" >
           
               <h4>Email     :  { this.state.email}</h4>
               <h4>Mobile No. :  { this.state.mobile_no }</h4>
               <h4>Address   :  { this.state.address }</h4>
               
               <div className="mb-3">
                <button onClick={this.onEditClient} className="btn btn-primary" style={{width:'100px', marginLeft:'250px'}}>
               Edit
                </button>
        
              </div>
               </div>
      )}
      <br></br>
      { !this.state.isClientExist && (  
                <form>
                <div className="form-group">
                    <label> Mobile No </label>
                    <input type="text" placeholder="mobile no" name="mobile_no" className="form-control" value={this.state.mobile_no} onChange={this.onChange}/>
                </div>

                <div className="form-group">
                    <label>Address:</label>
                    <input type="text" placeholder="address" name="address" className="form-control" value={this.state.address} onChange={this.onChange}/>
                </div>

              

                <button className="btn btn-success" onClick={this.saveClient}>Save</button>
            </form>
      )}
    </div>
        );
    }
}

export default AddClientComponent;