import { createStore, combineReducers, applyMiddleware } from "redux";
import logger from "redux-logger";
import thunk from "redux-thunk";
import { composeWithDevTools } from "redux-devtools-extension";

import {
  userSignupReducer,
  userSigninReducer
 
 
} from "./reducers/userReducers";

import {
  addProfileReducer,
  fetchCompanyReducer

}from "./reducers/companyReducer";


import {
  fetchQuotationReducer,
  addQuotationReducer

}from "./reducers/quotationReducer";
 

import {
  fetchServiceReducer,
  addServiceReducer

}from "./reducers/serviceReducer";
// combined reducers
const reducers = combineReducers({
  userSignup: userSignupReducer,
  userSignin: userSigninReducer,
  companyAddProfile: addProfileReducer,
  fetchQuotation : fetchQuotationReducer,
  addQuotation :  addQuotationReducer,
  fetchService : fetchServiceReducer ,
  addService :addServiceReducer,
  fetchCompany : fetchCompanyReducer

  
});

const store = createStore(
  reducers,
  composeWithDevTools(applyMiddleware(logger, thunk))
);

export default store;
